.registerContainer{
    display: flex;
    width: 99%;
    height: 99%;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    z-index: 1
}
.titulo {
    font-size: 3vh;
    text-align: center;
    margin: 1vh;
}
form {
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
}
input.Light::placeholder {
    color: white;
}
.form-input{
    border-radius: 5px;
    padding: 1vh;
    text-align: center;
    font-size: 2vh;
}
.coll {
    margin-bottom: 3vh;
}
.btnBack {
    position: absolute;
    width: 5vh;
    height: 5vh;
    display: flex;
    position: absolute;
    align-items: center;
    justify-content: center;
    bottom: 5vw;
    right: 3vh;
    transition: all 500ms ease;
    z-index: 1000;
    font-size: 2vh;
}

@keyframes haciaArriba {
    from {
        height: 70vh;
    }
    to {height: 300vh;}
}@keyframes haciaAbajo {
    from {
        height: 300vh;
    }
    to {height: 70vh;}
}